/* Global CSS */
body {
    margin: 0;
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-style: normal;
    background-color: #1a1a1a; /* Dark background */
    color: #e0e0e0; /* Light text color */
}

.btn {
    font-family: "Roboto";
    display: inline-block;
    padding: 14px 32px;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    border-radius: 4px;
    transition: background-color 0.5s;
}

.btn-outline-primary {
    color: #5e3bee; /* Primary color */
    background-color: transparent;
    border: 1px solid #5e3bee; /* Primary color */
}

.btn-outline-primary:hover {
    color: #fff; /* White */
    background-color: #5e3bee; /* Primary color */
}

.btn-primary {
    color: #fff; /* White */
    background-color: #5e3bee; /* Primary color */
    border: 1px solid #5e3bee; /* Primary color */
}

.btn-primary:hover {
    color: #5e3bee; /* Primary color */
    background-color: #fff; /* White */
}

/* Navbar Style Start */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 85.333px;
    background: #333; /* Dark background */
    box-shadow: 0px 5.333px 80px 0 rgba(0, 0, 0, 0.1);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 1000; /* Ensure navbar stays on top */
}

.navbar--items > ul {
    list-style: none;
    display: flex;
    align-items: flex-start;
    gap: 42.667px;
    text-decoration: none;
}

.navbar--items ul > li > a {
    text-decoration: none;
    color: #e0e0e0; /* Light text color */
}

.navbar--content {
    color: #e0e0e0; /* Light text color */
}

.navbar--active-content {
    color: #5e3bee; /* Primary color */
}

/* Navbar Styler Ends */

/* Main Headings */
h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    color: #fff; /* White */
}

/* Paragraphs */
p {
    margin: 0;
    color: #e0e0e0; /* Light text color */
}

/* Hero Section Style */
.hero--section {
    padding: 133.333px 85.333px 133.333px;
    align-items: center;
    justify-content: space-between;
    gap: 32px;
    background-color: #282938; /* Darker background */
    color: #fff; /* White */
}

.hero--section-description {
    color: #e0e0e0; /* Light text color */
}

/* Skills Section Style */
.skills--section {
    padding: 149.33px 85.33px;
    justify-content: center;
    align-items: flex-start;
    gap: 106.667px;
    background-color: #282938; /* Darker background */
    color: #fff; /* White */
}

.skills--section--heading {
    color: #fff; /* White */
}

.skills--section--card {
    background: #333; /* Dark background */
    border-radius: 10.6px;
}

.skills--section--card:hover {
    border-bottom: 4px solid #5e3bee; /* Primary color */
}

/* About Us */
.about--section {
    padding: 133.3px 85.3px;
    gap: 114.6px;
    background-color: #282938; /* Darker background */
    color: #fff; /* White */
}

/* My Portfolio Starts */
.portfolio--section {
    padding: 112px 85px;
    flex-direction: column;
    align-items: flex-start;
    gap: 80px;
    background-color: #282938; /* Darker background */
    color: #fff; /* White */
}

.portfolio--section--card {
    background: #333; /* Dark background */
    box-shadow: 0 0 32px 0 rgba(0, 0, 0, 0.5);
}

.portfolio--link {
    color: #5e3bee; /* Primary color */
    border-bottom: 1.33px solid #5e3bee; /* Primary color */
}

.portfolio--section--card:hover path {
    stroke: #5e3bee; /* Primary color */
}

/* Testimonial */
.testimonial--section {
    padding: 112px 84.3px;
    flex-direction: column;
    align-items: flex-start;
    gap: 80px;
    background-color: #282938; /* Darker background */
    color: #fff; /* White */
}

.sections--heading {
    color: #fff; /* White */
}

.testimonial--section--card {
    background: #333; /* Dark background */
    border: 1.33px solid #5e3bee; /* Primary color */
}

/* Contact Section */
.contact--section {
    padding: 150px 85px;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 64px;
    background-color: #282938; /* Darker background */
    color: #fff; /* White */
}

.contact--form--container {
    width: 40%;
}

.contact--label {
    color: #fff; /* White */
}

.contact--input {
    background-color: #333; /* Dark background */
    border: 1.333px solid #5e3bee; /* Primary color */
    color: #e0e0e0; /* Light text color */
}

.select {
    background-color: #333; /* Dark background */
    color: #e0e0e0; /* Light text color */
    border: 1.333px solid #5e3bee; /* Primary color */
}

.checkbox--label {
    color: #fff; /* White */
}

.checkbox--label input[type="checkbox"] {
    border-color: #5e3bee; /* Primary color */
}

/* Footer */
.footer--container {
    padding: 106.667px 85.333px;
    flex-direction: column;
    align-items: center;
    background-color: #282938; /* Darker background */
    color: #fff; /* White */
}

.footer--items ul > li > a {
    color: #5e3bee; /* Primary color */
}

.footer--social--icon ul {
    color: #fff; /* White */
}

.divider {
    background: rgba(224, 224, 224, 0.1); /* Lighter border color */
}

/* Responsive Screens */
@media only screen and (max-width: 1200px) {
    /* Add your responsive styles here */
}

/* Hamburger menu */
.nav__hamburger {
    display: none;
}

/* Ensure the hamburger menu is displayed on smaller screens */
@media screen and (max-width: 1200px) {
    .nav__hamburger {
        display: flex;
    }
}
